0.2);\n    width: 100%;\n    height: 100%;\n    transition-duration: .2s;\n}\n\n.search {\n    height: $height;\n    width: 100%;\n    z-index: 950;\n    display: flex;\n    position: relative;\n    &:focus-within {\n        input[type=text] {\n            background: hsl(0,0%, 20%);\n            // border: 3px solid hsl(0,0%,18%);\n            outline: none;\n            border-bottom-left-radius: 0;\n        }\n        #results {\n            max-height: 300vh;\n            border-top-left-radius: 0;\n        }\n        & + a.unfocus {\n            display: block;\n        }\n    }\n    // flex-direction: row-reverse;\n    // padding-left: 15px;\n    input[type=text] {\n        z-index: 960;\n        background: hsl(0, 0%, 14%);\n        border: none;\n        flex: 1 1 auto;\n        border-radius: 0;\n        margin: 0;\n        padding: 0;\n        padding-left: 12px;\n        width: 10px;\n        color: white;\n        font-size: 20px;\n        border-top-left-radius: 15px;\n        border-bottom-left-radius: 15px;\n        box-shadow: 0 0 5px 0 rgba(0,0,0,.6);\n        transition-duration: .25s;\n        &:hover {\n            background: hsl(0, 0%, 18%);\n        }\n    }\n    button {\n        z-index: 970;\n        transition-duration: .1s;\n        // width: calc(.5 * var(--sz-header-height));\n        width: $height;\n        background: hsl(0, 0%, 14%);\n        border: none;\n        padding: 0;\n        margin: 0;\n        border-radius: 0;\n        border-top-right-radius: 15px;\n        border-bottom-right-radius: 15px;\n        cursor: pointer;\n        i {\n            // width: calc(.5 * var(--sz-header-height));\n            // line-height: calc(.5 * var(--sz-header-height));\n            width: $height; line-height: $height;\n            color: white;\n        }\n        &:hover {\n            background: hsl(0, 0%, 18%);\n        }\n        &:active {\n            background: hsl(0,0%, 20%);\n        }\n    }\n    #results {\n        // display: none;\n        max-height: 0;\n        overflow: hidden;\n        position: absolute;\n        // transform: translateY(-100%);\n        top: $height;\n        left: 0;\n        width: calc(100% - 35px);\n        // padding: 10px;\n        background: hsl(0, 0%, 14%);\n        border-top-left-radius: 10px;\n        border-bottom-left-radius: 10px;\n        border-bottom-right-radius: 10px;\n        box-shadow: 0 0 15px 0 rgba(0,0,0,.6);\n        z-index: 960;\n        transition-duration: .25s;\n        font-size: 16px;\n        a {\n            display: block;\n            padding: 10px 12px;\n            color: inherit;\n            text-decoration: none;\n            // &:first-child {\n            //     margin-top: 0;\n            // }\n            &:hover, &:focus { \n                background: hsl(0, 0%, 18%);\n                box-shadow: 0 0 5px 0 rgba(0,0,0,.3);\n            }\n            &:focus {\n                box-shadow: inset 0 0 0 5px white;\n            }\n            &:active {\n                background: hsl(0,0%, 20%);\n            }\n            @at-root .touchevents #results a {\n                // border-bottom: 1px solid rgba(255,255,255,.1);\n                border-top: 1px solid rgba(255,255,255,.1);\n                &:hover { \n                    background: none;\n                    box-shadow: none;\n                }\n                &:active {\n                    background: none;\n                }\n            }\n        }\n    }\n}\n@media only screen and (max-width: 850px) {\n    .search {\n        // height: calc(.7 * var(--sz-header-height));\n        button {\n            // width: calc(.7 * var(--sz-header-height));\n            background: hsl(0, 0%, 18%);\n            // i {\n            //     // width: calc(.7 * var(--sz-header-height));\n            //     // line-height: calc(.7 * var(--sz-header-height));\n            // }\n        }\n    }\n}\n\n@media only screen and (max-width: 1100px) {\n    .container {\n        margin-right: 15px;\n    }\n}\n\n@media print {\n    .container {\n        display: none;\n    }\n}\n"],"sourceRoot":""}]);
