AAA,CACA,cAAA,CAHA,aAAA,CASA,gBAAA,CAHA,YAAA,CALA,oBAAA,CASA,uCAAA,CANA,uBAGR,CAKiB,0DAEL,uCAlCW,CAiCX,uCAFZ,CAMQ,0CAEI,2BAAA,CACA,4BAAA,CAFA,cAAA,CAGA,eAHZ,CAMQ,yCACI,UAJZ,CAQQ,uCAII,oBAAA,CAHA,4BAAA,CACA,cAAA,CACA,eALZ,CASQ,8DASI,mBAAA,CAHA,iBAAA,CAEA,gCAAA,CADA,uBAVZ,CAcY,oEAEI,UAAA,CADA,aAAA,CAEA,kBAZhB,CAeY,yEAEI,kBAAA,CADA,uCA3EM,CAiFN,WAAA,CAHA,MAAA,CAIA,mBAAA,CAAA,gBAAA,CALA,iBAAA,CAEA,KAAA,CAIA,uCAAA,CAHA,UAVhB,CAgByB,4DACL,uCAtFG,CAuFH,sBAdpB","sourcesContent":["\n\n$box-shadow-color: hsla(0, 0%, 0%, 0.08);\n$box-shadow--default: 0 4px 20px 0 $box-shadow-color;\n$box-shadow--expanded: 0 8px 30px 0 $box-shadow-color;\n\n.card {\n    word-wrap: break-word;\n    width: calc(100%);\n    padding: 10px 0 0;\n    margin: 0 0 30px;\n    display: flex;\n    justify-content: center;\n    color: var(--clr-font);\n\n    @media (min-width: 600px) {\n        width: calc(50% - 15px);\n    }\n\n    @media (min-width: 1000px) {\n        width: calc(33.333% - 20px);\n    }\n\n    .content {\n        display: block;\n        text-decoration: none;\n        color: inherit;\n        cursor: pointer;\n        width: calc(100% - 20px);\n        background: var(--clr-bg-primary);\n        padding: 15px;\n        box-shadow: $box-shadow--default;\n        border-radius: 15px;\n        min-height: 200px;\n        transition: transform 0.2s, box-shadow 0.2s;\n\n        @at-root html.no-touchevents .card:hover .content {\n            transform: scale(1.008) translate(0, -5px);\n            box-shadow: $box-shadow--expanded;\n        }\n\n        .category {\n            font-size: 18px;\n            color: var(--clr-wgg-orange);\n            font-family: var(--fnt-title);\n            font-weight: 600;\n        }\n\n        .timeago {\n            color: hsl(0, 0%, 50%);\n\n        }\n\n        .title {\n            font-family: var(--fnt-title);\n            font-size: 24px;\n            font-weight: 700;\n            word-wrap: break-word;\n        }\n\n        .force-thumbnail-aspect-ratio { \n            // CLARIFICATION:\n            // This workaround replicates the behavior of the experimental feature 'aspect-ratio'\n            // SOURCE:\n            // https://pqina.nl/blog/presenting-images-in-an-aspect-ratio-with-css/\n            //\n            position: relative;\n            width: calc(100% + 50px);\n            transform: translate(-25px, -25px);\n            margin-bottom: -15px;\n\n            &::after {\n                display: block;\n                content: '';\n                padding-bottom: 47%; // aspect ratio\n            }\n\n            .thumbnail {\n                box-shadow: $box-shadow--default;\n                border-radius: 15px;\n                position: absolute;\n                left: 0;\n                top: 0;\n                width: 100%;\n                height: 100%;\n                object-fit: cover;\n                transition: transform 0.2s, box-shadow 0.2s;\n                // background-color: hsl(0, 0%, 80%);\n\n                @at-root html.no-touchevents .card:hover .thumbnail {\n                    box-shadow: $box-shadow--expanded;\n                    transform: scale(1.008);\n                }\n            }\n        }\n    }\n}\n"],"sourceRoot":""}]);
