ADA,iBAAA,CAEA,OAAA,CACA,SArBR,CAyBI,qDAGI,YAAA,CAKA,aAAA,CAFA,eAAA,CADA,QAAA,CADA,SAAA,CAHA,iBAAA,CAMA,iBAAA,CALA,UAAA,CAOA,WAvBR,CA0BI,mEACI,aAxBR,CA2BI,0DAKI,0BAAA,CACA,iBAAA,CAEA,cAAA,CAPA,oBAAA,CAEA,WAAA,CACA,YAAA,CAGA,WAAA,CAEA,uBAAA,CAPA,UAlBR,CA4BI,sEACI,SAAA,CACA,oBA1BR","sourcesContent":["\n// ***********************\n// Flickity Styles\n.flickity-slide-scoped-css-5mf3i {\n    .flickity-enabled {\n        position: relative\n    }\n\n    .flickity-enabled:focus {\n        outline: 0\n    }\n\n    .flickity-viewport {\n        overflow: hidden;\n        position: relative;\n        height: 100%;\n    }\n\n    .flickity-slider {\n        position: absolute;\n        width: 100%;\n        height: 100%\n    }\n\n    .flickity-enabled.is-draggable {\n        -webkit-tap-highlight-color: transparent;\n        -webkit-user-select: none;\n        -moz-user-select: none;\n        -ms-user-select: none;\n        user-select: none\n    }\n\n    .flickity-enabled.is-draggable .flickity-viewport {\n        cursor: move;\n        cursor: -webkit-grab;\n        cursor: grab\n    }\n\n    .flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {\n        cursor: -webkit-grabbing;\n        cursor: grabbing\n    }\n\n    .flickity-button {\n        position: absolute;\n        background: hsla(0, 0%, 100%, 0.3);\n        border: none;\n        color: hsla(0, 0%, 0%, 0.8);\n        filter: brightness(100%);\n        transition: 0.15s background-color; //0.15s filter; // BUG: Filter doesnt work with css transitions\n\n        &:hover {\n            background: hsla(0, 0%, 100%, 0.5);\n            cursor: pointer\n        }\n\n        .backdropfilter & {\n            background: hsla(0, 0%, 0%, 0.1);\n            color: hsla(0, 0%, 100%, 1);\n            backdrop-filter: blur(8px);\n\n            &:hover {\n                background: hsla(0, 0%, 0%, 0.2);\n            }\n        }\n    }\n\n    .flickity-button:focus {\n        outline: 0;\n        box-shadow: 0 0 0 5px #19f\n    }\n\n    .flickity-button:active {\n        filter: brightness(70%);\n    }\n\n    .flickity-button:disabled {\n        opacity: .3;\n        cursor: auto;\n        pointer-events: none\n    }\n\n    .flickity-button-icon {\n        fill: currentColor\n    }\n\n    .flickity-prev-next-button {\n        top: 50%;\n        width: 44px;\n        height: 44px;\n        border-radius: 50%;\n        transform: translateY(-50%)\n    }\n\n    // Custom positioning\n    .flickity-prev-next-button.previous {\n        left: 10px;\n        \n        @media (min-width: 1080px) {\n\t\t    left: calc((100% - 1080px) / 2 + 10px)\n\t    }  \n    }\n\n    .flickity-prev-next-button.next {\n        right: 10px;\n\n        @media (min-width: 1080px) {\n\t\t    right: calc((100% - 1080px) / 2 + 10px)\n\t    }\n    }\n\n    .flickity-rtl .flickity-prev-next-button.previous {\n        left: auto;\n        right: 10px\n    }\n\n    .flickity-rtl .flickity-prev-next-button.next {\n        right: auto;\n        left: 10px\n    }\n\n    .flickity-prev-next-button .flickity-button-icon {\n        position: absolute;\n        left: 20%;\n        top: 20%;\n        width: 60%;\n        height: 60%\n    }\n\n    .flickity-page-dots {\n        position: absolute;\n        width: 100%;\n        bottom: -25px;\n        padding: 0;\n        margin: 0;\n        list-style: none;\n        text-align: center;\n        line-height: 1;\n        z-index: 100;\n    }\n\n    .flickity-rtl .flickity-page-dots {\n        direction: rtl\n    }\n\n    .flickity-page-dots .dot {\n        display: inline-block;\n        width: 10px;\n        height: 10px;\n        margin: 0 8px;\n        background: var(--clr-font);\n        border-radius: 50%;\n        opacity: .25;\n        cursor: pointer;\n        transition-duration: .2s;\n    }\n\n    .flickity-page-dots .dot.is-selected {\n        opacity: 1;\n        transform: scale(1.1);\n    }\n}\n"],"sourceRoot":""}]);
